	module.exports = {"data":{"markdownRemark":{"html":"<p>Only one more day of HEWeb2015. Tonight our big social event was at the Harley Davidson Museum, which was an amazing place. The old pre-World War II bikes are just simply amazing and beautiful. It's a good thing they don't make bikes like that anymore, otherwise I would need to own one. Yum!</p>\n<p>There were also some pretty intense games of Giant Jenga and Connect Four.</p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_001.jpg\" alt=\"Higher Ed Web Social Event 1\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_002.jpg\" alt=\"Higher Ed Web Social Event 2\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_003.jpg\" alt=\"Higher Ed Web Social Event 3\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_004.jpg\" alt=\"Higher Ed Web Social Event 4\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_005.jpg\" alt=\"Higher Ed Web Social Event 5\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_006.jpg\" alt=\"Higher Ed Web Social Event 6\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_007.jpg\" alt=\"Higher Ed Web Social Event 7\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_008.jpg\" alt=\"Higher Ed Web Social Event 8\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_009.jpg\" alt=\"Higher Ed Web Social Event 9\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_010.jpg\" alt=\"Higher Ed Web Social Event 10\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_011.jpg\" alt=\"Higher Ed Web Social Event 11\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_012.jpg\" alt=\"Higher Ed Web Social Event 12\"></p>\n<p><img src=\"https://s3-us-west-2.amazonaws.com/assets.kshermphoto.com/images/2015/HighEdWeb-Big-Social-Event_013.jpg\" alt=\"Higher Ed Web Social Event 13\"></p>\n<p>Peace<br>-- KS<br>Web Developer at Benedictine University near Chicago</p>","frontmatter":{"title":"-Photos- HEWEB15 Big Social Event!","author":"Kevin D. Sherman","date":"October 06, 2015"}}},"pathContext":{"slug":"/2015-10-06-heweb15-social-event/"}}

